<template>
  <section class="pc-container">
    <div class="pcoded-content pt-2 pb-4">
      <!-- <div class="page-header">
        <div class="page-block">
          <div class="row align-items-center">
            <div class="col-md-6">
              <div class="page-header-title">
                <h5 class="m-b-10">Partes Procesales</h5>
              </div>
              <ul class="breadcrumb">
                <li class="breadcrumb-item">
                  <router-link to="/sisge">Inicio</router-link>
                </li>
                <li class="breadcrumb-item">Demandantes</li>
              </ul>
            </div>
          </div>
        </div>
      </div> -->
      <div class="row py-3">
        <h5 class="text-dark fw-bold">Partes Procesales</h5>
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card shadow">
            <div class="card-header">
              <h5 class="text-secondary">Lista de Demandantes</h5>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table class="table" id="miTabla1">
                  <thead>
                    <tr>
                      <!-- <th>#</th> -->
                      <th>Documento</th>
                      <th>Nombre / Razon Social</th>
                      <!-- <th>Direccion</th> -->
                      <th>Telefono</th>
                      <th>Email</th>
                      <th class="text-center">Interacciones</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(persona, index) in personas" :key="index">
                      <!-- <td>
                        <div class="circle">{{ persona.nombre.charAt(0) }}</div>
                      </td> -->
                      <td>{{ persona.documento }}</td>
                      <td>{{ persona.nombre }}</td>
                      <!-- <td>{{ persona.direccion }}</td> -->
                      <td>{{ persona.telefono }}</td>
                      <td>{{ persona.correo }}</td>
                      <td class="text-center py-2">
                        <div class="d-flex">
                          <div class="tooltip-container">
                            <router-link
                              :to="{ name: 'historial/registro-demandante', params: { doc: persona.documento } }"
                              class="tooltip-trigger">
                              <i data-feather="edit"></i>
                            </router-link>
                            <div class="custom-tooltip">Registrar historial</div>
                          </div>
                          <div class="tooltip-container">
                            <router-link :to="{ name: 'demandante/informacion', params: { doc: persona.documento } }"
                              class="tooltip-trigger">
                              <i data-feather="file-text"></i>
                            </router-link>
                            <div class="custom-tooltip">Datos del demandante</div>
                          </div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import feather from 'feather-icons';

import { mapActions } from 'vuex';
export default {
  name: 'expedienteAll',
  created() {
    this.listarDemandantes();
  },
  data() {
    return {
      demandantes: [],
      dataTable: null,
    };
  },
  computed: {
    personas() {
      const personasMap = new Map();

      this.demandantes.forEach((demandante) => {
        const key = demandante.tipo_persona === 'natural'
          ? demandante.nat_dni
          : demandante.jur_ruc;
        if (!personasMap.has(key)) {
          personasMap.set(key, {
            documento: key,
            nombre: demandante.tipo_persona === 'natural'
              ? `${demandante.nat_apellido_paterno} ${demandante.nat_apellido_materno} ${demandante.nat_nombres}`
              : demandante.jur_razon_social,
            direccion: demandante.dir_calle_av,
            telefono: demandante.tipo_persona === 'natural'
              ? demandante.nat_telefono
              : demandante.jur_telefono,
            correo: demandante.tipo_persona === 'natural'
              ? demandante.nat_correo
              : demandante.jur_correo,
            exp_id: demandante.exp_id,
          });
        }
      });
      return Array.from(personasMap.values());
    },
  },
  methods: {
    ...mapActions(['get', 'post']),
    listarDemandantes() {
      this.get({
        url: this.$store.getters.get__url + '/demandante',
        token: this.$store.getters.get__token,
      })
        .then((response) => {
          console.log(response);
          this.demandantes = response.data;
          this.initDataTable();
        })
        .catch((errors) => { });
    },
    initDataTable() {
      if (this.dataTable) {
        this.dataTable.destroy();
      }

      // Inicializamos DataTable con los datos
      this.$nextTick(() => {
        this.dataTable = $('#miTabla1').DataTable({
          language: {
            url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
          },
        });
        feather.replace(); // Recargar los iconos
      });
    }
  },
  beforeDestroy() {
    if (this.dataTable) {
      this.dataTable.destroy();
    }
  },
};
</script>

<style scoped>
td {
  white-space: normal !important;
  word-wrap: break-word;
}

@media (min-width: 768px) {
  .table-responsive {
    overflow: hidden !important;
  }
}

.circle {
  width: 30px;
  height: 30px;
  background-color: #7267EF;
  color: white;
  font-weight: 600;
  border-radius: 50%;
  text-align: center;
  line-height: 30px;
}

.tooltip-container {
  position: relative;
}

.tooltip-trigger {
  cursor: pointer;
  padding: 7.5px;
  border: none;
  background-color: #7267EF;
  color: #fff;
  position: relative;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.55rem;
}

.custom-tooltip {
  width: 10rem;
  position: absolute;
  background-color: #6c757d;
  color: #fff;
  padding: 8px;
  border-radius: 4px;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.3s linear;
  z-index: 1;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.tooltip-trigger:hover+.custom-tooltip {
  visibility: visible;
  opacity: 1;
  top: 50%;
  right: calc(100% - 5px);
  transform: translate(0, -50%);
}

.custom-tooltip::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent #6c757d;
}
</style>
